<template>
  <div id="exam" style="width: 1100px;margin-left: 15%">
    <el-tabs v-model="activeName">
      <el-tab-pane label="我的考试" name="first">
        <div style="margin-top: 30px">
          <ul style="list-style: none;padding: 0;">
            <li v-for="(exam,index) in array"
                style="margin-left: 20px;width: 320px;padding-bottom: 20px;display:inline-block;padding-right: 20px">
              <div style="border: 1px solid #b3b3b3;padding: 20px;">
                <p style="font-size: 20px">{{ exam.name }}</p>
                <p style="font-size: 10px">开始时间：{{ exam.gmt_start }}</p>
                <p style="font-size: 10px">截止时间：{{ exam.gmt_end }}</p>
                <p style="font-size: 10px">作业状态：{{ exam.job_status }}</p>
              </div>
              <div style="border: 1px solid #b3b3b3;height: 40px;padding: 10px;background-color: #e8eeda">
                <el-button type="success" size="mini" style="float: right;margin-top: 5px">{{
                    exam.value
                  }}
                </el-button>
              </div>
            </li>
          </ul>
        </div>
      </el-tab-pane>
      <el-tab-pane label="待批考试" name="second">
        <div>
          <el-table
            :data="tableData"
            style="width: 100%">
            <el-table-column
              prop="title"
              label="考试标题"
              width="380">
            </el-table-column>
            <el-table-column
              prop="pending"
              label="待批">
            </el-table-column>
            <el-table-column
              prop="gmt_start"
              label="互评开始日期">
            </el-table-column>
            <el-table-column
              prop="gmt_end"
              label="互评截止日期">
            </el-table-column>
            <el-table-column
              label="操作">
              <template slot-scope="scope">
                <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
export default {
  name: "Exam",
  data() {
    const exam = {
      name: '第一次考试',
      gmt_start: '2021-12-1 00:00',
      gmt_end: '2021-12-7 00:00',
      job_status: '未提交',
      value: '考试'
    }
    return {
      activeName: 'first',
      array: Array(10).fill(exam),
      tableData: [{
        title: '第一次考试',
        pending: '0',
        gmt_start: '2021-12-1 00:00',
        gmt_end: '2021-12-7 00:00',
      }, {
        title: '第二次考试',
        pending: '0',
        gmt_start: '2021-12-1 00:00',
        gmt_end: '2021-12-7 00:00',
      }]
    };
  },
  methods: {
    handleClick(row) {
      console.log(row);
    }
  }
}
</script>

<style scoped>
p {
  margin: 0px;
  padding-bottom: 10px;
}
</style>



